body {
    font-family: "Book Antiqua", Palatino, "Palatino Linotype", "Palatino LT STD", Georgia, serif;
    font-weight: 300;
    letter-spacing: 0.1px;
    padding: 0;
    margin: 0px auto;
    background: white;
    color: white;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
}

html,
body {
    height: 100%;
    color: black;
}

.coming--soon {
    color: #fff;
    background-color: #303336;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;

    .coming--soon_body {
        display: flex;
        flex-direction: column;
        text-decoration: none;
        font-size: 1.5rem;
        color: rgb(255, 255, 255);
        height: 100vh;
        justify-content: center;
        z-index: 1;
        letter-spacing: 18px;

        .coming--soon_body_text {
            display: flex;
            flex-direction: column;
            align-items: center;
            cursor: pointer;

            .body_text--big-x {
                padding-bottom: 2rem;
                font-size: 4rem;
            }

            .body_text--silly {
                font-size: 15px;
                letter-spacing: normal;
                padding-top: 10px;
                width: 90%;
            }
        }
    }

    &:after {
        content: "";
        position: absolute;
        width: 200vw;
        height: 200vh;
        left: -50%;
        top: -50%;
        background-image: url(data:image/png;base64,);
        animation: grain 1.5s steps(6) infinite;
    }
}

.extras {
    display: flex;
    flex-direction: column;
    cursor: default;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    position: fixed;
    top: 0;
    left: 0;
    padding: 1rem;
    z-index: 99999999;

    .title {
        display: flex;
        padding-top: 15px;
        justify-content: space-evenly;
        color: #6f6f6f;
    }

    .logo {
        display: flex;
        flex-direction: column;
        opacity: .75;
        -webkit-transition: opacity 250ms;
        transition: opacity 250ms;

        &:hover {
            opacity: 1;
        }

        span {
            width: 10px;
            height: 10px;
            background: black;
            margin: .2rem;
        }

        span:nth-of-type(1) {
            opacity: .75;
        }

        span:nth-of-type(2) {
            opacity: .50;
        }

        span:nth-of-type(3) {
            opacity: .25;
        }
    }
}
